import * as React from "react"
import "../styles/global.scss"
import { StaticImage } from "gatsby-plugin-image"
import Footer from "../components/Footer"
import Delimiter from "../components/Delimiter"
import { Hr } from "../styles/Utility.styled"


const MentionLegalesPage = () => {
  return (
    <>
      <div className="bg-image-wrapper">
        <StaticImage className="bg-image" loading="eager" src="../images/forest_colorized.jpg" alt="" layout="fullWidth" />
        <div className="container padding-y-3 flex-column-center-start gap-m white" >
          <h1 className="page-title">Mentions Légales</h1>
          <Hr />
        </div>
         <Delimiter absolute bottom right /> 

      </div>
      <section id="mentions_legales" className="container padding-y-3">

        <p>
          Conformément aux dispositions de la loi n° 2004-575 du 21 juin 2004 pour la confiance en l'économie numérique, il est précisé aux utilisateurs du site foixelagage.fr l'identité des différents intervenants dans le cadre de sa réalisation et de son suivi.
        </p>
        <h3>Edition du site</h3>
        <p>Le présent site, accessible à l’URL foixelagage.fr (le « Site »), est édité par :</p>
        <p>Léon Ries, résidant 1 Route de Foix, 09000 Ganac, France</p>
        <h3>Hébergement</h3>
        <p>Le Site est hébergé par la société GitHub, Inc. , situé 88 Colin P Kelly Jr St,
          San Francisco, CA 94107, États-Unis, (contact telephonique: +1 (877) 958-8742)</p>
        <h3>Directeur de publication</h3>
        <p>Le directeur de publication est : Léon Ries.</p>
        <h3>Nous contacter</h3>
        <p>
          Par téléphone : +33780012233 <br />
          Par email : foixelagage@gmail.com <br />
          Par courrier : 1 Route de Foix , 09000 Ganac, France
        </p>
      </section >
      <Footer />
    </>
  )
}

export default MentionLegalesPage

export const Head = () => <title>Mentions légales</title>